import React, { Component } from 'react'
import {connect} from 'react-redux';
import * as actionCreator from './redux/actionCreator'

class Filter extends Component {
    render() {
        const { filterMode} = this.props;
        return (
            <select
                className="word"
                value={filterMode}
                onChange={evt => this.props.setFilterMode(evt.target.value)}>
                <option value="Show_All">Show All</option>
                <option value="Show_Memoried">Show Memoried</option>
                <option value="Show_Forgot">Show Forgot</option>
            </select>
        )
    }
}

const mapstate = state => ({filterMode : state.filterMode});
export default connect(mapstate, actionCreator)(Filter);