import React, { Component } from 'react'
import { connect } from 'react-redux';
import * as actionCreator from './redux/actionCreator'
import axios from "axios";

class Form extends Component {
    constructor(props){
        super(props)
        this.state = {
            txtEn : '',
            txtVn : ''
        }

    }
    addWord(){
        const {txtEn, txtVn} = this.state;
        const URL = 'http://server2912.herokuapp.com/word/';
        axios.post(URL,{en: txtEn, vn: txtVn})
        .then(res => {
            const {success, word} = res.data;
            if (!success) return alert("Can not add word");
            this.props.addWord(res.data.word)
        })
        this.setState({txtEn: '', txtVn: ''})
    }
    toggleForm(){
        
    }
    render() {
        const {txtEn, txtVn} = this.state;
        if(!this.props.shouldShowForm) return (
            <button
                className='btn btn-success' style={{width: 200, margin: 10}}
                onClick={() => this.props.toggleForm()}>
                +
            </button>
        )
        return (
            <div className="form-group word-from" >
                <input
                    placeholder="English"
                    className="form-control"
                    value={txtEn}
                    onChange={evt => this.setState({txtEn : evt.target.value})}/>
                <br />
                <input
                    placeholder="Vietnamese"
                    className="form-control"
                    value={txtVn}
                    onChange={evt => this.setState({txtVn : evt.target.value})}/>
                <br />
                <div className="btn-container">
                    <button 
                        className="btn btn-success"
                        onClick={()=>this.addWord()}>
                        Add word
                    </button>
                    <button
                        className="btn btn-danger"
                        onClick={() => this.props.toggleForm()}>
                        Cancel
                    </button>
                </div>
            </div>
        )
    }
}

const mapstate = state => ({ shouldShowForm: state.shouldShowForm });
export default connect(mapstate, actionCreator)(Form);