import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreator from './redux/actionCreator'
import axios from "axios"

class Word extends Component {
    // toggleWord(_id, isMemorized){
    //     const URL = 'http://localhost:4000/word/' + _id;
    //     axios.put(URL, {isMemorized})
    //     .then(response => this.props.toggleWord(_id))
    // }
    // removeWord(_id){
    //     const URL = 'http://localhost:4000/word/' + _id;
    //     axios.delete(URL)
    //     .then(response => this.props.removeWord(_id))
    // }
    render() {
        const { word } = this.props;
        return (
            <div className="word" >
                <div className="word-container">
                    <h3 className="text-success">{word.en}</h3>
                    <h3 className="text-danger">
                        {word.isMemorized ? '----' : word.vn}
                    </h3>
                </div>
                <div className="btn-container">
                    <button
                        className={word.isMemorized ? 'btn btn-danger' : 'btn btn-success'}
                        onClick={() => this.props.toggleWord(word._id, !word.isMemorized)}
                        >
                        {word.isMemorized ? 'Forgot' : 'Memoried'}
                    </button>

                    <button
                        className="btn btn-warning"
                        onClick={() => this.props.removeWord(word._id)}>
                        Remove
        </button>
                </div>
            </div>
        )
    }
}
export default connect(null,actionCreator)(Word);